const main = () => import('./') //懒加载
import home from '../home/router'
import ads from '../advertiser/router'
import geo from '../topGeo/router'
export default {
  name: "main",
  path: "main",
  component: main,
  redirect:{
    name:'home'
  },
  subRoutes: [
    home,
    ads,
    geo,
  ]
};



