<template>
   <div>
       <el-table ref="table" :data="tableData" stripe border class="commGridTable">
           <el-table-column :class="style.gridColumn" label="Rank" align="center" prop="rank" width="100px">
           </el-table-column>
           <el-table-column :class="style.cellDiv" label="Advertiser" header-align="center">
               <template slot-scope="scope">
                   <div :class="style.adverDiv">
                      <img v-if="scope.row.logo" :src="scope.row.logo" :class="style.img">
                      <a :href="scope.row.page_url" target="blank" :class="style.titleName">{{scope.row.name?scope.row.name:scope.row.page_id}}</a>
                   </div>
               </template>
           </el-table-column>
           <el-table-column label="Activity" align="center">
               <template slot-scope="scope">
                   <el-progress :percentage="scope.row.percent*100" :show-text="false"></el-progress>
               </template>
           </el-table-column>
           <el-table-column label="Creatives" align="center" width="200px" prop="ads_count">
           </el-table-column>
       </el-table>
   </div>
</template>
<script>
import style from '@/css/ads.css'
export default {
    data(){
        return{
            style,
        }
    },
    props:{
        tableData: null, //数据源
    },
    methods:{
       //将数据设置为追踪
       favorite(index){
           this.$emit('setFavorite',index);
       },
       //点击名字可跳转
       nameClick(data,name){
          this.$emit('nameClick',data,name);
       }
    }
}
</script>



