<template>
    <div :class="style.homeDiv" class="home">
        <div :class="style.homeCon">
           <el-input placeholder="Keyword or Package Name" type="text" :class="style.searchInput" @change="searchChange" v-model="keyword"></el-input>
           <el-date-picker v-model="timeSelect" type="daterange" align="right" unlink-panels range-separator="-" start-placeholder="start" 
           end-placeholder="end" :picker-options="pickerOptions2" :class="style.searchInput" @change="timeChange">
           </el-date-picker>
           <el-select v-model="geo" placeholder="请选择" :class="style.searchInput" @change="countryChange">
              <el-option label="All Country" value=""></el-option>
              <el-option-group v-for="group in countryList" :key="group.label" :label="group.label">
                 <el-option v-for="item in group.options" :key="item.value" :label="item.label" :value="item.key">
                 </el-option>
              </el-option-group>
           </el-select>
        </div>
        <ul :class="style.showUL" v-loading="loading">
            <li v-for="(item,index) in selectData" :key="`${index}`" :class="style.everyLI">
                <div v-html="item.code"></div>
            </li>
        </ul>
        <common-pagination :total="total" :pageSize="pageSize" @pageNumChange="pageNumChange"></common-pagination>
    </div>
</template>
<script>
import style from '@/css/home.css'
import {mapActions} from 'vuex'
import {getAdsData} from './mutation-types'
import countryList from  '@/data/country.json'
import CommonPagination from '#com/common-pagination'
export default {
    data(){
        return{
            style,
            selectData: [],
            keyword: '', //搜索关键字
            geo: '', //国家搜索
            begin: '', //开始时间
            end: '', //结束时间
            pickerOptions2: {
             shortcuts: [{
              text: 'Last 3 Days',
              onClick(picker) {
                const end = new Date();
                const start = new Date();
                start.setTime(start.getTime() - 3600 * 1000 * 24 * 3);
                picker.$emit('pick', [start, end]);
              }
            }, {
              text: 'Last 7 Days',
              onClick(picker) {
                const end = new Date();
                const start = new Date();
                start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
                picker.$emit('pick', [start, end]);
              }
            }, {
              text: 'Last 30 Days',
              onClick(picker) {
               const end = new Date();
               const start = new Date();
               start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
               picker.$emit('pick', [start, end]);
              }
            }]
          },
          timeSelect: '', //时间选择
          countryList,
          total: 0, //总数
          pageSize: 24,
          page: 1, //当前页
          loading: false, //展示加载中
        }
    },
    methods:{
        ...mapActions({
            getAdsData,
        }),
        async getData(){
            this.loading = true;
            let result = await this.getAdsData({
                keyword: this.keyword,
                geo: this.geo,
                begin: this.begin,
                end: this.end,
                page: this.page
            });
            if(!result) return;
            let data = JSON.parse(result);
            this.selectData = data.data;            
            this.total = data.total;
            this.$nextTick(()=>{
               FB.XFBML.parse();
               setTimeout(()=>{
                   this.loading = false;
               },10000);
            })
        },
        //搜索改变
        searchChange(){
           this.getData();
        },
        //时间改变
        timeChange(){
            if(this.timeSelect && this.timeSelect.length == 2){
               this.begin = parseInt(this.timeSelect[0].getTime()/1000);
               this.end = parseInt(this.timeSelect[1].getTime()/1000);
               this.getData();
            }
        },
        //国家改变
        countryChange(){
            this.getData();
        },
        //页数改变
        pageNumChange(val){
           this.page = val;
           this.getData();
        }
    },
    components:{
        CommonPagination,
    },
    mounted() {      
        let start = new Date();
        this.timeSelect = [new Date(start.getTime() - 3600 * 1000 * 24 * 30),start];
        if(this.timeSelect && this.timeSelect.length == 2){
            this.begin = parseInt(this.timeSelect[0].getTime()/1000);
            this.end = parseInt(this.timeSelect[1].getTime()/1000);
            this.getData();
        }       
    },
}
</script>




