<template>
   <div>
       <el-table ref="table" :data="tableData" stripe border class="commGridTable">
           <el-table-column :class="style.gridColumn" label="Rank" align="center" prop="rank" width="100px">
           </el-table-column>
           <el-table-column :class="style.cellDiv" label="Country" header-align="center">
               <template slot-scope="scope">
                  <div :class="style.adverDiv">
                      <span :class="getIcon(scope.row)" class="flag-icon"></span>
                      <span>{{scope.row.geo_name}}</span>
                  </div>
               </template>
           </el-table-column>
           <el-table-column label="Activity" align="center">
               <template slot-scope="scope">
                   <el-progress :percentage="scope.row.percent*100" :show-text="false"></el-progress>
               </template>
           </el-table-column>
           <el-table-column label="Creatives" align="center" width="200px" prop="ads_count">
           </el-table-column>
       </el-table>
   </div>
</template>
<script>
import style from '@/css/ads.css'
export default {
    data(){
        return{
            style,
        }
    },
    props:{
        tableData: null, //数据源
    },
    methods:{
       //获取图标
       getIcon(val){
          return `flag-icon-${val.geo_code}`
       }
    }
}
</script>



