<template>
    <div :class="style.adsDIv">
        <div :class="style.homeCon" class="advertiser">
           <el-select v-model="timeSelect" placeholder="请选择" @change="timeChange">
              <el-option value="3" label="Last 3 Days"></el-option>
              <el-option value="7" label="Last 7 Days"></el-option>
           </el-select>
           <div :class="style.timeDiv">
               <span>{{startTime}}</span>
               <span>-</span>
               <span>{{endTime}}</span>
           </div>
        </div>
       <grid :class="style.grid" :tableData="tableData" v-loading="loading"></grid>
    </div>
</template>
<script>
import Grid from './grid'
import style from '@/css/ads.css'
import {mapActions} from 'vuex'
import {getListData} from './mutation-types'
import '../../../../node_modules/flag-icon-css/css/flag-icon.min.css'
export default {
    data(){
        return{
           style,
           timeSelect: '3', //时间选择
           startTime: '', //开始时间
           endTime: '', //结束时间
           tableData: [], //表格数据源
           loading: false, 
        }
    },
    components:{
        Grid,
    },
    methods:{
        ...mapActions({
            getListData,
        }),
        //时间改变
        timeChange(){
           this.dealTime();
           this.getData();
        },
        //处理时间
        dealTime(){
           let endTime = new Date();           
           this.endTime = this.getTime(endTime);
           if(this.timeSelect == '3'){
              let startTime = new Date(endTime.getTime() - 3600 * 1000 * 24 * 3);
              this.startTime = this.getTime(startTime);
           }               
           else if(this.timeSelect == '7'){
               let startTime = new Date(endTime.getTime() - 3600 * 1000 * 24 * 7);
               this.startTime = this.getTime(startTime);
           }
        },
        async getData(){
            this.loading = true;
            let result = await this.getListData({
                days: this.timeSelect
            });
            if(!result) return;
            let data = JSON.parse(result);
            if(data.errmsg == 'Success'){
               this.tableData = data.data;
            }
            this.loading = false;
        },
        getTime(date){
           let year = date.getFullYear();
           let month = date.getMonth() + 1;
           let strDate = date.getDate(); 
           if (month >= 1 && month <= 9) {
              month = "0" + month;
           }
           if (strDate >= 0 && strDate <= 9) {
              strDate = "0" + strDate;
           }
           return `${year}-${month}-${strDate}`;
        }
    },
    mounted() {
        this.dealTime();
        this.getData();
    },
}
</script>






